@import "../Variables.module.scss";

.horizontalContainer {
    padding: 40px 40px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    img {
        width: 203px;
        cursor: pointer;
    }
    button {
        display: flex;
        justify-content: center;
        align-items: center;
        padding-top: 15px;
        padding-bottom: 15px;
    }
}

@media screen and (max-width: 500px) {
    .horizontalContainer {
        padding-left: 25px;
        padding-right: 25px;
        img {
            width: 153px;
        }
    }
}